.assortment-details-dialog {
  .dialog-sec {

    &.merchandise-val {
      height: auto;
      width: 100%;
      font-size: 13px;
      line-height: 1;
      overflow: hidden;


      .prod-region {
        span {
          padding-right: 10%;
        }
      }
    }

    img {
      margin-right: 8px;
    }
  }

  .paper.info-grid h2 {
    font-weight: 600;
    opacity: 0.6;
    padding-top: 3px;
  }

  .paper.info-grid ul.body > li {
    flex-basis: auto;

    &:nth-child(2),
    &:nth-child(3), {
      margin-bottom: 25px;
    }

    h3 {
      opacity: 0.6;
    }
  }

  .MuiDialogContent-root {
    display: flex;
    flex-direction: column;

    & > div:first-child {
      display: flex;
      flex-direction: row;
      .info-grid {
        flex-basis: unset;
        width: 350px;

        .body {
          flex-direction: row;
        }
      }

      .notifications {
        width: 300px;
        flex: 1;

        h3{
          position: relative;
          padding-left: 25px;

          svg {
            color: var(--color-highlight-navbar);
            position: absolute;
            top: -5px;
            left: -5px;
          }
        }
      }
    }

    div:last-child {
      margin-top: var(--box-margin);
    }
  }

  .paper.simple {
    h3 {
      font-size: 16px;
      font-weight: 600;
      line-height: 1;
      color: var(--text-primary-color);
    }

    li {
      margin-bottom: 15px;
    }
  }
}

.alert-dialog-modal {
  .MuiDialog-paperWidthMd {
    width: 480px;
  }

  .paper {
    p {
      font-size: 0.875rem;
    }
  }
}
