.dashboard {
  display: flex;

  .title {
    display: flex;
    flex-direction: row;

    h1 {
      flex: 1;
      font-weight: 500;

      svg {
        mask: url(/images/icons/edit.svg) no-repeat 0 5px;
        background-color: var(--text-blue);
        margin: 0 8px;
      }

      label {
        color: var(--text-color-light);
        font-size: 12px;
        font-weight: normal;
        padding-top: 13px;
      }
    }

    button {
      margin-left: var(--box-margin);
      font-weight: 600;
      box-shadow: none;

      &.send-button {
        min-width: 160px;
        font-size: 1rem;
        line-height: 1.38;
        color: #fbfbfb;
        border-radius: var(--box-radius-8);
        padding: 12px 16px;
        text-transform: capitalize;
        background-color: var(--color-highlight-navbar);

        svg {
          margin-right: 5px;
        }

        &:hover {
          filter: brightness(90%);
        }
      }

      &.save-button {
        color: var(--color-highlight-navbar);
        font-size: 16px;
      }
    }
  }

  & > .body {
    display: flex;
    flex-direction: row;
    height: 100%;

    & > .main-container {
      width: 100%;
    }

    .main {
      display: flex;
      flex-direction: column;
      height: 100%;
      position: relative;
      width: calc(100% - var(--sidebar-width) - 15px);
    }
  }

  .sidebar {
    width: var(--sidebar-width);
    display: flex;
    flex-wrap: wrap;
    max-height: 100%;
    margin-left: 15px;

    & > * {
      margin-top: var(--box-margin-10);
      width: 100%;
    }

    & > *:first-child {
      margin-top: 0;
    }

    .add-product {
      font-size: 10px;
      line-height: 20px;

      svg {
        margin-right: 10px;
        font-size: 16px;
      }
    }

    .paper {

      &.map-section {
        width: 100%;

        .autocomplete {
          margin-bottom: var(--box-margin);
          border-radius: var(--box-radius-small);
          overflow: hidden;
          border: 1px solid var(--border-color);

          &.Mui-focused {
            border: 1px solid var(--color-highlight2);
          }
          div.MuiAutocomplete-endAdornment button {
            display: none;
          }
          div.MuiAutocomplete-inputRoot {
            padding: 3px 10px 3px 25px;
            background: url(/images/icons/location-themed.svg) no-repeat 8px 50%;

            &::before {
              border-bottom: none;
            }
            .MuiAutocomplete-tag {
              background-color: var(--color-highlight2);
              border-radius: var(--box-radius-small);
              color: var(--background-color);
              margin: 3px;
              font-size: 13px;
              font-weight: 600;
              svg {
                color: var(--background-color);
              }
            }
          }

          .MuiInput-underline {
            &::after {
              border-bottom: none;
            }
          }
        }

        h4 {
          text-transform: uppercase;
          font-size: 12px;
          color: var(--text-color-light);
          margin-bottom: 5px;
        }

        .map {
          flex: 1;
          border-radius: var(--box-radius-small);
          overflow: hidden;
          height: 117px;
        }
      }

      &.trending-section {

        div {
          display: flex;
          flex-wrap: wrap;
          margin: -10px;
          height: 250px;

          a {
            width: 96px;
            height: 96px;
            margin: 5px;
            overflow: hidden;
            border-radius: var(--box-radius-small);
            position: relative;

            img {
              width: 100%;
              height: 100%;
            }

            span {
              background: url(/images/icons/instagram.svg) no-repeat;
              width: 30px;
              height: 30px;
              display: inline-block;
              position: absolute;
              left: 5px;
              top: 5px;
              opacity: 0.7;
            }
          }
        }
      }

      &.reviews-section {

        .review-head {
          display: flex;
          justify-content: space-between;
          align-items: center;
        }

        .sentiment {
          display: inline-block;
          position: relative;
          top: 5px;
          padding: 2px;
          border-radius: 5px;
          height: 25px;
          margin-left: 5px;
          font-size: 12px;
          font-weight: normal;
          text-align: center;

          &.happy {
            background-color: #a4e2bc;
            color: #57ad77;
          }
          &.sad {
            background-color: #eca2b3;
            color: #e36781;
          }
          &.neutral {
            background-color: #a7c0e6;
            color: #6081c5;
          }
          svg {
            font-size: 20px;
          }
          span {
            display: inline-block;
            vertical-align: super;
            margin-right: 5px;
          }
        }
        & > div {
          position: relative;

          &.hide-scroll {
            height: 100px;
          }

          .sentiment-graph {
            background: url(/images/rendered/sentiment.png) no-repeat center #fff;
            background-size: contain;
            position: absolute;
            left: 0;
            top: 0;
            right: 0;
            bottom: 0;
            z-index: 1;
            display: none;
          }
          &.show-sentiment {
            overflow: hidden;
            .sentiment-graph {
              display: block;
            }
          }
          .review {
            margin-top: var(--box-margin);
          }
          .review:first-child {
            margin-top: 0;
          }
          .rating {
            text-indent: -500px;
            mask: url(/images/icons/star.svg) repeat-x -4px 0;
            background-color: gold;
            display: inline-block;
            &.rating-5 {
              width: 115px;
            }
            &.rating-4 {
              width: 91px;
            }
            &.rating-3 {
              width: 66px;
            }
            &.rating-2 {
              width: 41px;
            }
            &.rating-1 {
              width: 18px;
            }
          }
          .when {
            color: var(--text-color-light);
            display: inline-block;
            font-size: 12px;
            margin-left: var(--box-margin);
          }
          .content {
            margin-top: 5px;
            font-size: 16px;
          }
        }
      }
    }
  }

  .kpis-section {
    display: flex;
    align-items: flex-start;
    margin-bottom: 10px;

    &.kpis-section-top {

      h5 {
        font-size: 10px;
        font-weight: normal;
        text-transform: uppercase;
        line-height: 12px;
        opacity: 0.7;
        margin-bottom: 5px;
      }

      .graph-right {

        h4 {
          font-size: 14px;
          font-weight: normal;
          line-height: 12px;
          text-transform: uppercase;
        }

        h5 {
          font-size: 9px;
          margin-bottom: 0;
        }

        h6 {
          font-size: 12px;
          line-height: 12px;
          font-weight: normal;
          color: var(--color-dark-grey);
          margin: 5px 0;
          text-transform: uppercase;
        }

        .body {
          li {
            width: 48%;
            margin: 10px auto 0 !important;

            p {
              font-weight: 500;
              font-size: 22px;
              line-height: 24px;

              &.positive-val,
              &.negative-val {
                width: 65px;
                margin: 0 auto;
              }

              &.value-p {
                font-size: 14px;
                line-height: normal;
                font-weight: normal;
              }
            }
          }
        }
      }

      .body {
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;

        li {
          width: 31%;
          margin-bottom: 15px;
          margin-top: 10px;

          &:nth-child(odd) {
            margin-left: 15%;
          }
        }
      }

      li.comparison-sec {
        flex-basis: 420px;

        .yearly-heading {
          font-size: 8px;
          line-height: 12px;
          letter-spacing: 8px;
          text-transform: uppercase;
          font-weight: bold;
          padding-left: 28px;

          &.demand-head {
            text-align: right;
            padding-right: 45px;
            padding-left: 0;
          }
        }

        h4 {
          font-size: 15px;
          line-height: 12px;
          font-weight: normal;
          color: var(--color-dark-grey);
        }

        li {
          padding-left: 10px;
          position: relative;
          font-size: 8px;
          line-height: 12px;

          &::before {
            content: "";
            position: absolute;
            width: 7px;
            height: 10px;
            left: 0;
            top: 0px;
          }

          &:first-child::before {
            background-color: rgba(224, 98, 98, 0.7);
          }

          &:last-child {
            margin-left: 7px;

            &::before {
             background-color: rgba(64, 138, 222, 0.7);
           }
          }
        }

        .body {
          li {
            width: 100%;
          }
        }
      }

      .paper.info-grid ul.body > li {
        margin-right: 1%;
        flex-flow: initial;
        flex-basis: auto;
      }

      .sale-sec {
        h3,
        h4 {
          line-height: 12px;
          font-weight: normal;
          color: var(--color-dark-grey);
        }

        .demand-label {
          position: absolute;
          bottom: 5px;
          font-size: 22px;
          line-height: 24px;
          text-align: center;

          &.plan-label {
            left: 6px;
          }

          &.pre-label {
            right: 2px;
          }

          span {
            font-size: 9px;
            display: block;
            color: rgba(52, 52, 52, 0.7);
            text-transform: uppercase;
            text-align: center;
            line-height: 12px;
          }
        }

        .center-val {
          position: absolute;
          font-size: 10px;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          -webkit-transform: translate(-50%, -50%);
          -moz-transform: translate(-50%, -50%);
          text-align: center;

          p {
            padding-left: 10px;
            font-size: 14px;
            line-height: 24px;
          }
        }

        h3 {
          font-size: 14px;
          margin-bottom: 5px;
        }

        h4 {
          font-size: 12px;
        }
      }

      .yearly-sec {
        h2,
        p {
          color: var(--text-primary-color);
        }

        h3 {
          color: var(--text-color-light);
        }
      }

      .total-section,
      .yearly {
        h2 {
          font-size: 37px;
          line-height: 24px;
        }
      }

      & > li {
        margin-left: var(--box-margin-30);
        height: 100%;
        border-radius: var(--box-radius-8);
        box-shadow: 0 4px 8px 0 rgba(49, 49, 74, 0.1);
        padding: var(--box-padding-16);

        &.heading-sec {
          padding: 0;
          box-shadow: none;
        }

        &:first-child {
          margin-left: 0;
        }

        &.yearly {
          h2 {
            opacity: 1;
            font-weight: 600;
          }
        }
        &.graph {
          padding: 0;
          height: 144px;

          img {
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }
}

.paper {
  &.simple {
    h2 {
      font-size: 12px;
      font-weight: normal;
      color: var(--text-color-light);
      text-transform: uppercase;
    }
    h3 {
      font-size: 16px;
      font-weight: 600;
      font-weight: normal;
      color: var(--text-color);
      margin-bottom: var(--box-margin);
    }
  }

  &.textured {
    .MuiTypography-root {
      color: var(--background-color);
    }
  }

  &.total-yearly-val {
    width: 370px;
  }

  &.bg-2020 {
    background: var(--background-section-2020);
    background-size: auto 100%;
    background-position: -3px 9px;
  }

  &.bg-2019 {
    background: var(--background-section-2019);
    background-position: 2px 13px;
  }

  &.textured-light {
    background: var(--background-section-highlighted-light);
    .MuiTypography-root {
      color: var(--background-color);
    }
  }

  &.graph-right {
    background: var(--background-section-chart);
    background-position: bottom;
    background-size: contain;
  }

  &.textured-extra-light {
    background: var(--background-section-highlighted-extra-light);
    .MuiTypography-root {
      color: var(--background-color);
    }
  }

  &.info-grid {
    flex-direction: row;

    h2 {
      font-size: 16px;
      font-weight: normal;
      text-transform: capitalize;
      line-height: 1;
      margin-right: 20px;
      opacity: 0.8;
    }
    ul.body {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;

      & > li {
        flex-grow: 1;
        flex-basis: 0;
        margin-right: 7px;

        &:first-child {
          margin-bottom: 15px;
        }

        h3 {
          font-size: 12px;
          font-weight: normal;
          text-transform: uppercase;
        }
        p {
          font-weight: 600;
          font-size: 16px;
          margin-top: 4px;
          line-height: 1;
        }
      }
    }
    &.col-2 {
      .body > :nth-child(1),
      .body > :nth-child(3n) {
        p {
          font-size: 20px;
        }
      }
    }
    &.col-3 {
      .body > :nth-child(1),
      .body > :nth-child(4n) {
        p {
          font-size: 24px;
        }
      }
    }
  }
}

.chart-head {
  margin-left: 0 !important;

  p {
    color: var(--color-highlight-navbar);
    margin: 0;
    margin-right: 10px;
  }

  .alert-sec {
    width: 111px;
    display: flex;
    align-items: center;

    svg {
      margin: 0 5px;
    }
  }
}

.button-section {
  display: flex;
  justify-content: flex-end;
  margin: 10px 0;

  svg {
    font-size: 0.875rem;
  }

  button {
    margin-left: var(--box-margin);
    font-weight: 600;
    box-shadow: none;

    &.send-button {
      min-width: 140px;
      font-size: 12px;
      font-weight: normal;
      height: 32px;
      line-height: 1;
      color: #fbfbfb;
      border-radius: var(--box-radius-8);
      padding: 0;
      background-color: var(--color-highlight-navbar);

      svg {
        margin-right: 5px;
      }

      &:hover {
        filter: brightness(90%);
      }
    }
  }

  .upload-btn {
    align-items: center;
    min-width: 140px;
    height: 32px;
    font-size: 12px;
    line-height: 32px;
    text-align: center;
    color: #fbfbfb;
    border-radius: var(--box-radius-8);
    padding: 0;
    background-color: var(--color-highlight-navbar);
    margin-left: 10px;
    cursor: pointer;
    text-transform: uppercase;

    &:hover {
      filter: brightness(90%);
    }
  }
}

.variance-val {
  h4,
  p {
    font-weight: normal;
    position: relative;

    &:before {
      position: absolute;
      left: 0;
    }

    &.positive-val {
      color: #5ADB86;

      &:before {
        content: url(/images/arrow-up.svg);
      }
    }

    &.negative-val {
      color: #E24E4E !important;

      &:before {
        content: url(/images/arrow-down.svg);
      }
    }
  }
}

.graph-container.paper {
  box-shadow: 0px 4px 8px rgba(49, 49, 74, 0.1), 0px -4px 5px rgba(49, 49, 74, 0.08);
}

.flex-row {
  flex-direction: row;
  display: inline-flex;
}
