.recommended-products {
  width: 100%;
  margin-top: 20px;
  padding-left: 31px;

  h3 {
    font-weight: normal;
    font-size: 20px;
    line-height: 16px;
    color: var(--text-primary-color);
    margin-bottom: 20px;
  }

  .products-list {
    display: flex;
    margin-bottom: 15px;
    width: 100%;
    overflow-x: auto;

    .product-list {
      margin-right: 20px;
      min-width: 240px;
      box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.07);
      background: #fff;
      border-radius: 8px;
      padding: 10px 15px;
      display: flex;
      position: relative;
      cursor: pointer;

      &:hover {
        background: rgba(0, 0, 0, 0.3);
        filter: blur(1px);
        -webkit-filter: blur(1px);

        &::after {
          content: url(/images/plus-hover.svg);
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          -webkit-transform: translate(-50%, -50%);
          -moz-transform: translate(-50%, -50%);
        }

      }
    }
  }
}

.merchandise-value {
  width: calc(100% - 56px);

  ul {
    width: 45%;
    margin-right: 5%;
    padding-left: 15px;

    &:first-child {
      li {
        padding-right: 15px;
        text-align: right;
      }
    }

    &:last-child {
      margin-right: 0;
      margin-left: 0;
      padding-left: 0;

      li {
        text-align: left;
      }
    }
  }

  h5,
  h6 {
    font-size: 10px;
    line-height: 10px;
    color: var(--text-primary-color);
    opacity: 0.5;
    font-weight: normal;
    margin-bottom: 5px;
  }

  h5 {
    margin-right: 5px;
    padding-right: 10px;
    text-align: right;
    border-right: 1px solid var(--text-primary-color);
  }

  .loc-result {
    font-size: 12px;
    line-height: 16px;
    color: var(--text-primary-color);
    font-weight: 500;
  }
}
