.hide {
  display: none;
}
.graph-circle {
  width: 3px;
  height: 3px;
  background-color: #000000;
  border-radius: 50%;
}
.dashboard {
  .assortments-section {
    display: flex;
    flex: 1;
    flex-direction: column;
    position: relative;

    .top {
      display: flex;
      justify-content: space-between;
      padding: 10px var(--box-padding-16);
      background-color: var(--background-color-section);
      border-radius: var(--box-radius-8) var(--box-radius-8) 0 0;
      align-items: center;
      margin-left: 22px;

      .top-assortments {
        display: flex;
        align-items: flex-end;

        span {
          margin-left: 25px;
          opacity: 0.5;
          font-size: 12px;
          font-weight: normal;
          line-height: 16px;
        }
      }

      h3 {
        font-weight: normal;
        color: var(--text-primary-color);
      }

      .date-filter-section {
        .MuiButton-root {
          padding: 0;
          min-width: 30px;

          &:nth-child(2) {
            padding: 4px 16px;
            border-radius: var(--box-radius-6);
            border: 1px solid rgba(49, 49, 74, .2);
            color: #1844A9;
            font-size: 12px;
            line-height: 1.33;
            font-weight: 500;
          }
        }

        .MuiFormControl-root {
          margin: 0;
          min-width: 94px;
          margin-left: 16px;
        }

        .MuiFilledInput-input {
          padding: 5px 12px 4px;
          font-size: 12px;
          line-height: 1.33;
          font-weight: 500;
          color: var(--text-white);
        }

        .MuiSelect-icon {
          right: 0px;
          color: white;
        }

        .MuiFilledInput-root {
          background: #0061CC;
          border-radius: var(--box-radius-4);
        }

        .MuiFilledInput-underline:before,
        .MuiFilledInput-underline.Mui-focused:after {
          border-bottom: none !important;
        }

        .MuiToggleButton-root {
          border: none !important;
          height: 25px;
          padding: 2px 5px;
          font-size: 12px;
          margin: 0;
          min-width: 100px;
          background: var(--text-blue);
          border-radius: 4px;

          &:first-child {
            margin-right: 0;
          }

          .MuiSvgIcon-root {
            font-size: 15px;
          }

          &.Mui-selected {
            background-color: var(--color-highlight2);
            color: var(--background-color);
          }
        }
      }
    }

    .paper.graph-container {
      display: flex;
      flex: 1;
      border-radius: 0;
      z-index: 1;
      padding: 20px 10px;

      .add {
        position: fixed;
        left: 28px;
        bottom: 28px;
      }

      .graph-header {
        height: auto;
        margin-top: -15px;
        overflow: hidden;
      }

      ul {
        flex: 1 1 auto;
        overflow-y: auto;
        height: 0px;
        height: 610px;

        li {
          height: 130px;
          display: flex;
          border-top: 1px solid var(--border-color);

          &.legend {
            border-top: none;
            margin-left: 127px;
            height: 50px;
          }
          &.legend.year {
            margin-bottom: -30px;
          }
          &.legend .recharts-cartesian-axis-tick-value {
            fill: var(--text-color);
            font-size: 14px;
            margin-top: 10px;
          }
          &.legend line.recharts-xAxis {
            display: none;
          }

          .custom-tooltip {
            padding: 5px 10px;
            background-color: var(--background-color);
            color: var(--text-color);
            border-radius: var(--box-radius-small);
            opacity: 0.8;
            box-shadow: var(--box-shadow-small);
            font-size: 12px;
            width: 350px;

            p {
              margin: 4px .5%;
              width: 49%;
              display: inline-flex;
            }
          }

          .details {
            width: 118px;
            padding: 10px 0;
            text-align: center;
            position: relative;
            cursor: pointer;
            display: flex;

            svg {
              position: absolute;
              top: 5px;
              right: -25px;
              z-index: 1;
            }

            &.information {
              .icon {
                position: absolute;
                top: 17px;
                left: 0px;
                color: #ffc065;
              }
            }

            img {
              height: 64px;
              display: none;
              border-radius: var(--box-radius-small);
            }
          }

          .graph {
            flex: 1;
          }
        }
      }
    }

    div.bottom {
      margin-left: 22px;

      button {
        border-radius: 0 0 var(--box-radius-small) var(--box-radius-small);
        flex: 0;
        margin-right: var(--box-margin);
        background-color: var(--color-highlight-navbar);

        svg {
          margin-right: 10px;
        }
      }

      .color-checkbox {
        border-radius: 0 0 var(--box-radius-small) var(--box-radius-small);
        background-color: var(--background-color-section);
        padding: 16px 25px;
        color: var(--text-primary-color);
        opacity: 0.6;
        font-size: 12px;
        line-height: 1.33;
        display: flex;
        justify-content: space-between;

        svg {
          width: 12px;
          height: 12px;
        }

        .MuiTypography-body1 {
          font-size: 14px;
          line-height: 16px;
          color: var(--text-primary-color);
        }

        .MuiIconButton-root {
          padding: 0 5px 0 0;
        }

        .MuiFormControlLabel-root {
          margin: 0;
        }
      }
    }
  }

  .sell_out_units_ytd {
    path {
      fill: #4171c6;
    }
  }

  .sell_out_units_ly {
    path {
      fill: #e8a369;
    }
  }

  .sell_out_units_sas {
    path {
      fill: #000000;
    }
  }

  .demand_units_ytd {
    path {
      fill: green;
    }
  }

  .demand_units_ly {
    path {
      fill: #CCCCCC;
    }
  }

  .demand_units_sas {
    path {
      fill: #ff0000;
    }
  }

  .shipments {
    path {
      fill: yellow;
    }
  }

  .info-icon {
    color: #E24E4E;
    font-size: 13px;
  }

  .assortments-recommendation-section {
    margin-top: var(--box-margin);
    display: flex;
    flex-direction: column;
    flex-basis: 135px;

    .add-product-section {
      overflow-x: scroll;
    }

    ul {
      flex: 1 1 auto;
      width: 0;

      display: flex;
      flex-direction: row;

      li {
        overflow: unset;
        margin-left: var(--box-margin);
        margin-top: var(--box-margin);
        position: relative;
        float: left;
        padding: 15px 15px 15px 50px;
        cursor: pointer;

        &:first-child {
          margin-left: 0;
        }
        .icon {
          position: absolute;
          left: 15px;
          top: 28px;
        }
        h6 {
          font-size: 10px;
          color: var(--text-color-light);
        }
        p {
          font-size: 14px;
          width: 100px;
          height: 45px;
          overflow: hidden;
        }
        transition: transform 0.1s ease-in-out;
        &:hover {
          transform: translate(0, -5px);
          filter: brightness(96%);
        }
      }
    }
  }
}

.loader-background {
  display: none;
}

.loader-body {
  .loader-background {
    background-color: rgba(0, 137, 0, 0.1);
    // background-color: rgba(0, 0, 0, 0.75);
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    z-index: 9;
    display: block;
  }

  #loader-image {
    display: block;
    position: absolute;
    z-index: 10;
    left: 40%;
    top: 40%;
  }
}

.show-alert {
  span {
    padding: 0;
    padding-right: 0px;
    font-size: .875rem;
  }

  .MuiSvgIcon-root {
    font-size: 1.2rem;
  }

  .MuiFormControlLabel-root {
    margin-left: 0px;
    margin-bottom: 5px;
    margin-right: 5px;
  }
}

.graph-section {

  .merchandise-val {
    width: 100%;
  }

  .MuiButtonBase-root {
    padding: 0;
  }

  .inputbox-icon {
    color: #DE9821 !important;

    .MuiSvgIcon-root {
      font-size: 16px;
    }
  }

  .checkbox-graph {
    padding: 9px 0px;
    width: 19px;
  }
}

.add-export {
  .export-csv {
    content: url(/images/export.svg);
    width: 22px;
    transition: all 300ms ease-in-out 0s;
    -webkit-transition: all 300ms ease-in-out 0s;
    -moz-transition: all 300ms ease-in-out 0s;

    &:hover {
      filter: brightness(90%);
    }

    button {
      padding: 0;
    }
  }
}
