div.navbar {
  width: 118px;
  background: var(--background-section-highlighted);
  position: fixed;

  display: flex;
  flex-direction: column;
  height: 100%;
  align-content: stretch;

  .spacer {
    flex: 1;
  }

  a.home {
    text-align: center;
    margin-bottom: 140px;
  }

  a.links {
    display: block;
    text-align: center;
    padding: 0 28px;
    text-decoration: none;
    border-right: 8px solid transparent;
    height: 56px;
    width: 100%;

    &:hover,
    &.active {
      border-right: 8px solid var(--color-highlight-navbar);
      background-color: rgba(255, 255, 255, 0.1);

      span {
        background-color: var(--color-highlight-navbar);
      }
    }

    span {
      height: 100%;
      display: block;
      text-indent: -500px;
      mask: url(/images/icons/calendar.svg) no-repeat 20px;
      background-color: var(--background-color);
    }

    &.exit {
      margin-bottom: 300px;
    }
    &.dashboard span {
      mask-image: url(/images/icons/dashboard.svg);
    }
    &.tasks span {
      mask-image: url(/images/icons/tasks.svg);
    }
    &.calendar span {
      mask-image: url(/images/icons/calendar.svg);
    }
    &.chat span {
      mask-image: url(/images/icons/chat.svg);
    }
    &.search span {
      mask-image: url(/images/icons/search.svg);
    }
    &.settings span {
      mask-image: url(/images/icons/settings.svg);
    }
    &.exit span {
      mask-image: url(/images/icons/exit.svg);
    }
  }
}
