.assortment-graph-dialog.dialog {
  div.MuiPaper-root, div.MuiDialogContent-root {
    background-color: var(--background-color-section);
  }
  .MuiDialogContent-root {
    display: flex;
    flex-direction: column;

    div.graph {
      img {
        width: 300px;
        display: block;
      }

      label {
        text-transform: uppercase;
        color: var(--text-primary-color);
        font-size: 12px;
        font-weight: 500;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.33;
        margin-bottom: 24px;
        display: flex;
        justify-content: space-between;
      }
    }
  }
}

.dialog-modal.dialog {
  .MuiDialog-paperWidthSm {
    max-width: 750px;
  }

  .MuiDialogTitle-root {
    padding: 10px;
  }

  .MuiDialogContent-root {
    padding: 0 25px 0px;
  }

