.MuiPaper-rounded {
  .MuiMenu-list {
    padding-left: 8px;
    padding-right: 8px;

    li {
      font-size: 12px;
      font-weight: 500;
      line-height: 1.33;
      color: var(--text-primary-color);
    }

    .MuiListItem-root.Mui-selected,
    .MuiListItem-root.Mui-selected:hover {
      background-color: transparent;
      border: 1px solid #0061cc;
      border-radius: 6px;
      box-shadow: 0 3px 7px 0 rgba(49, 49, 74, 0.07);
    }
  }
}

.merchandise-val {
  width: 90%;
  font-size: 9px;
  line-height: 10px;
  display: flex;
  text-align: left;
  height: 110px;
  overflow-y: auto;
  overflow-x: hidden;
  text-transform: uppercase;

  .heading {
    color: var(--text-primary-color);
    opacity: 0.5;
    padding-top: 0;
  }

  .prod-region {
    width: 45%;
    display: inline;
    margin-right: 5%;

    .heading {
      border-right: 1px solid var(--text-primary-color);
    }

    span {
      padding-right: 5px;
      text-align: right;
    }
  }

  .prod-brand {
    margin-right: 0;
    text-align: left;
  }

  .bold {
    font-weight: bold;
  }

  span {
    padding-top: 4px;
    display: block;
  }
}

.modal-graph {
  h2 {
    font-weight: normal;
    font-size: 28px;
    line-height: 1;
    padding-bottom: 18px;
    color: var(--color-dark-grey);
    text-transform: uppercase;
    text-align: center;
    display: block;
    width: 50%;
    margin: 0 auto 7px;
    position: relative;

    &::after {
      content: "";
      width: 390px;
      height: 3px;
      background: #F0F0F0;
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      -webkit-transform: translateX(-50%);
      -moz-transform: translateX(-50%);
    }
  }

  .merchandise-val {
    width: auto;
    min-width: 100px;
    height: auto;
    margin-bottom: 10px;
    overflow: hidden;

    .heading {
      font-size: 10px;
    }

    span {
      font-size: 13px;
      line-height: 1;
    }

    .prod-region {
      width: 48%;
      margin-right: 1%;
    }

    .prod-brand {
      width: 48%;
    }
  }
}

.chart-modal-val {
  margin-bottom: 30px;

  .chart-modal {
    position: relative;

    .chart-label {
      font-size: 13px;
      line-height: 12px;
      color: var(--color-dark-grey);
      margin-left: 5px;
      position: absolute;
    }
  }

  .vertical-sec {
    display: flex;
    border-right: 1px solid #C4C4C4;
    padding-right: 4px;
    margin-right: 4px;
    flex-direction: column;
    justify-content: space-between;

    p {
      font-size: 10px;
      line-height: 12px;
      color: #A6A6A6;
    }
  }

  .MuiDialogActions-root {
    margin-top: 20px;
    justify-content: center;

    button {
      background-color: var(--color-highlight-navbar);
      font-size: 14px;
      font-weight: 600;
      line-height: 1.38;
      color: #fbfbfb;
      border-radius: 4px;
      padding: 5px 25px;

      &:hover {
        filter: brightness(90%);
      }
    }
  }

  .color-state {
    margin-top: 5px;
    text-transform: uppercase;
    display: block;
    padding-right: 10px;
    border-right: 0.8px solid rgba(49, 49, 74, 0.8);

    span {
      width: 15px;
      height: 3px;
      margin-right: 12px;
    }

    p {
      margin-bottom: 0px;
      font-size: 10px;
      line-height: 12px;
    }
  }

  .chart-fileds {
    h3 {
      color: var(--color-highlight-navbar);
      font-size: 18px;
      font-weight: 600;
      text-align: center;
      display: block;
      margin-bottom: 0;
      line-height: 12px;
    }

    h6 {
      font-size: 10px;
      line-height: 12px;
      color: rgba(49, 49, 74, 0.5);
      font-weight: normal;
    }

    ul {
      padding: 0 10px;

      li {
        display: flex;
        font-size: 12px;
        padding: 5px 0;
        color: var(--text-color-light);
        justify-content: space-around;
        width: 96%;
        margin: 0 auto;


        &.no-editable {

          margin-bottom: 10px;
          padding-top: 0;

          input {
            font-weight: bold;
            font-size: 16px;
            line-height: 12px;
            text-align: center;
            border: none;
            background: transparent;
            box-shadow: none;
            width: 50%;
            color: rgba(49, 49, 74, 0.5);
          }
        }

        input {
          background: #F0F0F0;
          border-radius: 2px;
          font-size: 10px;
          color: var(--text-primary-color);
          line-height: 12px;
          border: 0.3px solid #C1C1C1;
          text-align: right;
          padding: 3px;

          &:focus {
            outline-width: 0;
            border-color: var(--color-highlight2);
          }

          &::-webkit-outer-spin-button,
          &::-webkit-inner-spin-button {
            -webkit-appearance: none;
            margin: 0;
          }
        }
      }
    }
  }
}
