.login {
  display: flex;
  flex-direction: row;
  height: 100%;

  .remove-color {
    color: transparent;
  }

  h1 {
    font-weight: 500;
  }

  .hero-section {
    width: 66.67%;
    background: url(/images/hero.jpg) no-repeat;
    background-size: cover;
    position: relative;
    background-position: center center;

    img {
      position: absolute;
      right: 0;
      top: 50%;
      margin-top: -75px; /* -(image_height/2) */
    }
  }
  .form-section {
    flex: 1;
    flex-direction: column;
    display: flex;
    justify-content: center;
    align-items: center;

    form {
      width: 350px;
      color: var(--text-color);

      h1 {
        margin-top: var(--box-margin);
      }

      .field-section {
        display: flex;
        flex-direction: column;

        label {
          margin-bottom: 8px;
          margin-top: 24px;
          line-height: 1;
        }

        input {
          border: 1px solid var(--border-input-color);
          border-radius: var(--box-radius-6);
          color: var(--text-color);
          padding: 14px 16px;
          font-size: 16px;
          &:focus {
            outline-width: 0;
            border-color: var(--color-highlight2);
          }

          &::-webkit-input-placeholder {
            color: var(--placeholder-color);
          }

          &::-moz-placeholder {
            color: var(--placeholder-color);
          }

          &:-ms-input-placeholder {
            color: var(--placeholder-color);
          }

          &:-moz-placeholder {
            color: var(--placeholder-color);
          }
        }

        a {
          text-align: right;
          margin-top: var(--box-margin-16);
          color: var(--text-primary-color);
          font-weight: 500;
          line-height: 1;
          float: right;
          padding-bottom: 1px;
          border-bottom: 1px solid;
        }
      }

      .button-section {
        margin-top: 50px;
        display: flex;
        flex-direction: column;
        align-items: center;

        .MuiButton-containedPrimary {
          background-color: var(--text-blue);
          border-radius: var(--box-radius-6);
          min-width: 160px;
          font-size: 17px;
          font-weight: 600;
          line-height: 1.29;
          box-shadow: none;
        }

        .MuiButton-textPrimary {
          color: var(--text-blue);
          line-height: 1.38;
          font-weight: 600;
          font-size: 16px;
        }

        button, a {
          margin-bottom: 0;
          padding: 13px 30px;
        }
      }
    }
  }
}
