.assortment-browser-dialog {
  .MuiDialogTitle-root {
    padding-top: 24px !important;
    padding-bottom: 30px !important;
  }

  h2.MuiTypography-root {
    font-size: 16px !important;
    font-weight: 600 !important;
    line-height: 1;
    color: var(--text-primary-color) !important;
  }

  .MuiListItem-container {
    border-bottom: 1px solid rgba(0, 0, 0, 0.07);
  }

  .MuiDialog-paper {
    .MuiList-root {
      background-color: var(--background-color) !important;

      .MuiTypography-body1 {
        font-size: 16px;
      }

      .MuiCollapse-container  {
        .MuiListItem-root {
          padding-left: 80px;
        }
      }
    }

    .MuiListItemIcon-root {
      min-width: auto;
      margin-right: var(--box-padding);
      svg {
        color: var(--color-highlight);
      }
    }
    .MuiListItemSecondaryAction-root {
      svg {
        color: #0C580C;
      }
    }
  }

  div.MuiTabs-scroller {
    & > span {
      background-color: transparent;
    }
  }
}

.result-val {
  position: relative;
  display: inline-block;
  margin-right: 10px;
  font-size: 8px;
  line-height: 1.2;
  color: #0061cc;
  text-transform: uppercase;
  border-bottom: 1px solid;

  &:last-child {
    color: #939393;
    border-bottom: none;

    &::after {
      content: "";
    }
  }

  &::after {
    content: "/";
    right: -7px;
    position: absolute;
  }
}

.breadcrumb-result {
  padding-bottom: 10px;
  margin-bottom: 0px;
  padding-top: 10px;

  &:empty {
    display: none;
  }
}

.assortment-modal {

  .MuiIconButton-edgeEnd {
    margin-right: 0;
  }

  .MuiTreeItem-root:focus > .MuiTreeItem-content {
    background-color: transparent;
  }

  .MuiTreeItem-content {
    border-bottom: 1px solid rgba(0, 0, 0, 0.07);
    padding-bottom: 10px;
    padding-top: 10px;
  }

  .MuiList-root .MuiTreeItem-root {
    border-bottom: none;
    padding: 0;
  }

  .MuiListItemText-primary {
    border-bottom: 1px solid;
    margin-left: 12px;
  }

  header.MuiPaper-root {
    border-radius: 8px;
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
    margin-bottom: 24px;
  }

  button.MuiTab-textColorPrimary {
    font-size: 14px;
    line-height: 1.43;
    color: var(--text-primary-color);
    border-radius: 8px;

    &.Mui-selected {
      background-color: #0061cc;
      color: var(--text-white);
    }
  }

  .MuiInput-root {
    width: 100%;
    margin-bottom: 10px;
  }

  .makeStyles-nested-142 {
    padding-left: 15px;
  }

  .MuiInputBase-input {
    border-radius: 8px;
    border: solid 1px rgba(49, 49, 74, 0.25);
    padding: 10px;
    padding-left: 52px;
    background-color: var(--text-white);
  }

  .MuiInput-underline:before {
    background: url(/images/search-icon.png) no-repeat;
    border-bottom: none !important;
    left: 15px;
    top: 50%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
  }

  .MuiInput-underline:after {
    border-bottom: none !important;
  }
}

.MuiTypography-body1.back-icon {
  margin-bottom: 10px;
  font-size: 0.875rem;
  display: flex;
  align-items: center;

  svg {
    cursor: pointer;
    margin-right: 3px;
  }

  span {
    cursor: pointer;
  }
}

.merchandise-list {

  .check-product {
    font-size: 14px;
  }

  .MuiTreeItem-iconContainer {
    margin-left: 2px;

    svg {
      font-size: 13px;
      color: var(--color-highlight-navbar);
    }
  }

  &.MuiList-root {
    button {
      color: #1b5e20;
    }
  }

  .MuiTreeItem-label {
    font-size: 10px;
    line-height: 20px;

    &:hover {
      background-color: transparent;
    }
  }

  .MuiIconButton-edgeEnd {
    margin-right: 0;
  }

  .MuiTreeItem-root.Mui-selected > .MuiTreeItem-content .MuiTreeItem-label:hover,
  .MuiTreeItem-root.Mui-selected:focus > .MuiTreeItem-content .MuiTreeItem-label,
  .MuiTreeItem-root.Mui-selected > .MuiTreeItem-content .MuiTreeItem-label {
    background-color: transparent;
  }
}

.sidebar-product {
  padding: var(--box-padding-16);
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.07);
  border-radius: 8px;
  background-color: #fff;

  div:empty {
    display: none;
  }

  .MuiList-root {
    margin-top: 5px;
    height: 135px;
    padding-top: 0;
    overflow-y: auto;

    &.search-result {

      .check-product {
        font-size: 14px;
      }

      .MuiListItemText-root {
        margin-top: 0;
        margin-bottom: 0;
      }

      .MuiListItemText-primary {
        border-bottom: none;
        font-size: 9px;
        line-height: 13px;
      }
    }

    &:empty {
      display: none;
    }
  }

  .MuiInput-root {
    width: 100%;
  }

  .MuiList-root {
    ul {
      max-width: 100%;
      margin-top: 5px;

      &.MuiTreeItem-group {
        margin-left: 5px;
      }
    }

    .MuiTreeItem-root {
      padding: 5px 0;
      border-bottom: 1px solid rgba(0, 0, 0, 0.07);

      &:last-child {
        border-bottom: none;
      }
    }

    .MuiIconButton-root {
      padding: 0;
      float: right;
    }

    button {
      color: var(--color-highlight-navbar);

      .filled-icon {
        width: 18px;
        height: 18px;
        margin-right: 3px;
      }
    }

    .MuiTreeItem-content:hover {
      background-color: transparent;
    }
  }

  input {
    border-radius: 8px;
    border: 1px solid rgba(49,49,74,.25);
    padding: 5px 5px 5px 28px;
    background-color: var(--text-white);
    background: url(/images/search-icon.png) no-repeat;
    background-position: 10px;
    background-size: 10px;
    font-size: 10px;
  }

  .tab-block {
    margin: 0 0 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: rgba(245, 246, 247, 0.5);
    border-radius: var(--box-radius-8);
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);

    span {
      background: var(--text-white);
      color: var(--text-primary-color);
      font-size: 10px;
      line-height: 20px;
      border-radius: var(--box-radius-8);
      flex-grow: 1;
      max-width: none;
      flex-basis: 0;
      flex-shrink: 1;
      padding: 3px 10px;
      text-align: center;
      text-transform: uppercase;

      &.active {
        background-color: var(--text-blue);
        color: var(--text-white);
      }
    }
  }
}
