:root {
  --background-color: rgba(235, 236, 239, 1);
  --background-color-section: rgba(255, 255, 255, 1);
  --background-color-section-highlighted: rgba(2, 28, 58, 1);
  --background-section-highlighted: url(/images/demin-background.png) repeat;
  --background-section-2020: url(/images/total-2020.svg) no-repeat #001B38;
  --background-section-2019: url(/images/total-2019.svg) no-repeat #001B38;
  --background-section-chart: url(/images/chart.png) no-repeat #ffffff;
  --background-section-highlighted-light: rgba(0, 33, 70, .8);
  --background-section-highlighted-extra-light: rgba(0, 33, 70, .6);
  --background-color-section-highlighted-hover: rgba(4, 48, 102, 1);

  --color-highlight: rgba(221, 86, 13, 1);
  --color-highlight-navbar: #de9821;
  --color-highlight2: rgba(1, 94, 198, 1);

  --color-dark-grey: #57575A;

  --border-color: rgb(204, 197, 203);
  --placeholder-color: rgba(49, 49, 74, 0.3);
  --border-input-color: rgb(49, 49, 74, 0.25);
  --text-color: rgba(50, 50, 50, 1);
  --text-blue: #0061cc;
  --text-white: rgba(255, 255, 255, 1);
  --text-primary-color: #31314a;
  --text-color-light: rgba(49, 49, 74, 0.6);

  --box-margin-10: 10px;
  --box-margin: 15px;
  --box-margin-16: 16px;
  --box-margin-30: 30px;
  --box-margin-40: 40px;
  --box-padding-16: 16px;
  --box-padding: 20px;
  --box-radius: 10px;
  --box-radius-4: 4px;
  --box-radius-6: 6px;
  --box-radius-8: 8px;
  --box-radius-small: 7px;
  --box-shadow: 0 0 8px 0 rgba(49, 49, 74, 0.2);
  --box-shadow-small: 0 0 5px 3px rgba(0, 0, 0, 0.1);

  --sidebar-width: 290px;
  --navbar-width: 118px;
}

* {
  box-sizing: border-box;
}

.flex-column {
  flex-direction: column;
}

.relative {
  position: relative;
}

.fs-14-hard {
  font-size: 14px !important;
}

.p-t-0 {
  padding-top: 0;
}

.p-b-0 {
  padding-bottom: 0;
}

.p-b-0-hard {
  padding-bottom: 0 !important;
}

.p-l-10 {
  padding-left: 10px;
}

.p-r-15 {
  padding-right: 15px;
}

.m-l-15 {
  margin-left: 15px;
}

.m-b-0-hard {
  margin-bottom: 0 !important;
}

.m-t-0 {
  margin-top: 0;
}

.m-b-9 {
  margin-bottom: 9px;
}

.m-b-10 {
  margin-bottom: 10px;
}

.m-b-10-hard {
  margin-bottom: 10px !important;
}

.m-b-15 {
  margin-bottom: 15px;
}

.m-b-15-hard {
  margin-bottom: 15px !important;
}

.m-b-26-hard {
  margin-bottom: 26px !important;
}

.m-r-8 {
  margin-right: 8px;
}

.m-r-10 {
  margin-right: 10px;
}

.m-r-15 {
  margin-right: 15px;
}

.theme-button {
  background-color: var(--color-highlight-navbar) !important;
  color: var(--background-color-section) !important;

  &:hover {
    filter: brightness(90%);
  }
}

.text-uppercase {
  text-transform: uppercase;
}

.text-transparent {
  color: transparent;
}

.text-center {
  text-align: center;
}

.text-white {
  color: var(--text-white);
}

.nowrap {
  white-space: nowrap;
}

.lh-1 {
  line-height: 1;
}

.d-flex {
  display: flex;
}

.align-center {
  align-items: center;
}

.align-start {
  align-items: flex-start;
}

.justify-center {
  justify-content: center;
}

.d-inline {
  display: inline;
}

.pointer {
  cursor: pointer;
}

ul,
ul li {
  margin: 0;
  padding: 0;
  text-indent: 0;
  list-style-type: none;
  display: block;
}

html,
body,
#root {
  height: 100%;
}

body {
  color: var(--text-color);
  background-color: #F5F6F7 !important;
}

.w-42 {
  width: 42px;
}

.full-width {
  width: 100%;
}

.hide-scroll {
  overflow-y: scroll;
  &::-webkit-scrollbar {
    background: transparent;
    width: 0px;
  }
}

.border {
  border: 1px dashed red;
}

.shadow,
.paper {
  box-shadow: var(--box-shadow);
}

.shadow-small {
  box-shadow: var(--box-shadow-small);
}

.p-9 {
  padding: 9px;
}

.container {
  margin-left: var(--navbar-width);
  padding: 56px 40px;
  display: flex;
  flex-direction: column;

  .title {
    margin-bottom: 50px;
  }

}

.paper {
  background-color: var(--background-color-section);
  border-radius: var(--box-radius);
  color: var(--text-color);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  padding: var(--box-padding);

  &.dots-h {
    position: relative;
  }

  .more {
    border-radius: var(--box-radius-small);
    background-color: transparent;
    padding: 10px;
    border: none;
    position: absolute;
    right: 10px;
    top: 18px;
    cursor: pointer;

    &:hover {
      background-color: var(--background-color);
      span {
        background-color: var(--color-highlight);
      }
    }

    &:focus {
      outline-width: 0;
    }

    span {
      mask: url(/images/icons/more.svg) no-repeat center;
      background-color: var(--background-color-section-highlighted);
      display: block;
      font-size: 0;
      width: 26px;
      height: 6px;
    }
  }
}

.more-icon {
  border-radius: var(--box-radius-small);
  background-color: transparent;
  padding: 10px;
  border: none;
  cursor: pointer;

  &:hover {
    background-color: var(--background-color);
    span {
      background-color: var(--color-highlight);
    }
  }

  &:focus {
    outline-width: 0;
  }

  span {
    mask: url(/images/icons/more.svg) no-repeat center;
    background-color: var(--background-color-section-highlighted);
    display: block;
    font-size: 0;
    width: 26px;
    height: 6px;
  }
}

button,
a {
  color: var(--background-color-section);

  &.MuiButton-containedPrimary {
    background-color: var(--color-highlight-navbar);

    &:hover {
      background-color: var(--color-highlight-navbar);
      filter: brightness(90%);
    }
  }
  &.MuiButton-containedSecondary {
    background-color: var(--color-highlight2);
    &:hover {
      background-color: var(--color-highlight2);
      filter: brightness(90%);
    }
  }

  &.MuiButton-textPrimary, &.MuiTab-textColorPrimary {
    color: var(--color-highlight);
  }
  &.MuiButton-textSecondary {
    color: var(--color-highlight2);
  }
  &.MuiTab-textColorPrimary {
    &.Mui-selected {
      color: var(--color-highlight);
    }
  }
}

svg {
  &.MuiSvgIcon-colorPrimary {
    color: var(--color-highlight);
  }
  &.MuiSvgIcon-colorSecondary {
    color: var(--color-highlight2);
  }
}

.dialog {
  div.MuiDialog-paper {
    border-radius: var(--box-radius);
    background-color: var(--background-color);
  }

  div.MuiDialogTitle-root {
    padding: 20px;

    h2 {
      font-size: 20px;
      font-weight: normal;
      color: var(--text-color);
      text-transform: initial;
      margin-top: 0;
      position: relative;

      button {
        position: absolute;
        top: -5px;
        right: 0;
      }
    }
  }
  div.MuiDialogContent-root {
    background-color: var(--background-color);
    padding: 0 40px 20px;
  }

  &.category-title .MuiDialogTitle-root {
    span {
    }
  }
}

/* Dialog Box */
.cu-confirm-box {
  h2,
  .MuiDialogContent-root {
    text-align: center;
  }

  .MuiDialogActions-spacing {
    justify-content: center;
    margin-bottom: 10px;
  }

  button {
    min-width: 100px;
  }

  .ok-button {
    background-color: var(--color-highlight-navbar);
  }
}
/* End Dialog Box */
